var Baobab = require('baobab');
var { dynamicNode } = Baobab;
var { filter } = require('lodash');
var getDocLabel = require('../lib/get_doc_label');
var isPackage = require('../../shared/is_package');
var isPackageMember = require('../../shared/is_package_member');

export default function initState(user) {
  window.quills = {};

  var initialState = {
    user,
    users: [],
    messages: [],
    entitlements: [],
    subdocs: dynamicNode({
      cursors: {
        docsById: ['autosave', 'docsById']
      },
      get: ({ docsById }) => filter(docsById, doc => doc.schema_name !== 'story')
    }),
    packageDocs: dynamicNode({
      cursors: {
        doc: ['doc'],
        docsById: ['autosave', 'docsById']
      },
      get: ({ doc, docsById }) => {
        if (isPackage(doc.head.contents)) {
          return Object.keys(docsById).reduce((docs, docId) => {
            var doc = docsById[docId];
            if (isPackageMember(doc.head.contents)) {
              docs[doc.uuid] = doc;
            }
            return docs;
          }, {});
        }
        return {};
      }
    }),
    docMissingEntry: dynamicNode({
      cursors: {
        doc: ['doc']
      },
      get: ({ doc }) => doc && !doc.entry_id
    }),
    readOnly: dynamicNode({
      cursors: {
        autosaveError: ['autosave', 'error'],
        docMissingEntry: ['docMissingEntry'],
        trashedAt: ['doc', 'head', 'contents', 'trashed_at'],
        socketError: ['socketError'],
        messages: ['messages']
      },
      get: ({ autosaveError, docMissingEntry, trashedAt, socketError, messages }) => (
        docMissingEntry   ||
        autosaveError     ||
        trashedAt != null ||
        socketError ||
        messages.some(m => m.forceRefresh)
      )
    }),
    docLabel: dynamicNode({
      cursors: {
        doc: ['doc']
      },
      get: ({ doc }) => getDocLabel(doc)
    }),
    subdocLabel: dynamicNode({
      cursors: {
        subdoc: ['subdoc']
      },
      get: ({ subdoc }) => subdoc ? getDocLabel(subdoc) : ''
    }),
    appWorking: dynamicNode({
      cursors: {
        autosaveDirty: ['autosave', 'dirty'],
        requestInProgress: ['requestInProgress']
      },
      get: ({ autosaveDirty, requestInProgress }) => {
        return (autosaveDirty && Object.keys(autosaveDirty).length > 0) || requestInProgress;
      }
    })
  };

  app.state = new Baobab(initialState, { pure: false, immutable: false });
}
