export default class WorkerQueue {
  constructor() {
    this.queue = [];
    this.isProcessing = false;
  }

  push(callback, ...args) {
    this.queue.push({ callback, args });
  }

  async process() {
    if (this.isProcessing) {
      // exit if already processing. we'll continue processing the queue after this one finishes
      return;
    }
    this.isProcessing = true;

    if (this.queue.length <= 0) {
      // exit nothing left to process
      this.isProcessing = false;
      return;
    }

    // process the first thing put in the queue (FIFO)
    while (this.queue.length > 0) {
      const { callback, args } = this.queue.shift();
      await callback(...args);
    }
    this.isProcessing = false;
  }
}
