const { pick } = require('lodash');
const Sentry = window.Sentry = require('@sentry/browser');
const { ExtraErrorData } = require('@sentry/integrations/dist/extraerrordata');
const ALLOW_URLS = [
  /stories\.usechorus\.com/,
  /chorus\.voxmedia\.com/,
  /vox-cdn\.com/,
  /anthem\.sb[\d]+\.k8s\.voxops\.net/,
];

export default function initSentry() {
  app.state.set('sentry', { inLine: [], inFlight: null });
  var config = app.config.sentry;
  var userContext = pick(app.state.get(['user']), [
    'id',
    'email',
    'fullOrUserName',
    'fullName',
    'lastAnnouncementReadAt',
    'lastAnnouncementSeenAt',
  ]);

  if (config && config.dsn) {
    Sentry.init({
      dsn: config.dsn,
      release: config.release,
      environment: app.env || process.env.NODE_ENV,
      integrations: [new ExtraErrorData({ depth: 3 })],
      allowUrls: ALLOW_URLS
    });
    Sentry.configureScope(scope => {
      scope.setUser(userContext);
    });
  }
}
