exports.activefield = function(node, target = 'input, textarea, [contenteditable]') {
  target = node.querySelector(target);

  var placeholder = node.querySelector('.ql-placeholder');

  if (target == null) {
    throw new Error('Failed to initialize activefield decorator, could not find target node');
  }

  var setClasses = function(hasFocus) {
    var hasContent;
    if ('value' in target) {
      hasContent = target.value.length > 0;
    } else if (placeholder && placeholder.textContent.length > 0) {
      hasContent = placeholder.textContent.length > 0;
    } else {
      hasContent = target.textContent.length > 0;
    }
    node.classList.toggle('has-content', hasContent);
    node.classList.toggle('has-focus', hasFocus);
    node.classList.toggle('has-content-or-focus', hasContent || hasFocus);
  };

  var onFocus = function() {
    setClasses(true);
  };

  var onBlur = function() {
    setClasses(false);
  };

  var onInput = function() {
    setClasses(document.activeElement === target);
  };

  var observer = new MutationObserver(onInput);
  observer.observe(target, {
    attributes: true,
    characterData: true,
    subtree: true
  });

  target.addEventListener('focus', onFocus);
  target.addEventListener('blur', onBlur);
  target.addEventListener('input', onInput);

  onInput();

  return {
    teardown: () => {
      target.removeEventListener('focus', onFocus);
      target.removeEventListener('blur', onBlur);
      target.removeEventListener('input', onInput);
      observer.disconnect();
    }
  };
};
