// strip leading and trailing junk (whitespace, <br>, and empty tags)
module.exports = function(html) {
  var root = document.createElement('div');

  root.innerHTML = html;

  // first pass: leading junk from firstChild
  removeWhitespace(root, /^\s+/, 'firstChild');

  // second pass: trailing junk from lastChild
  removeWhitespace(root, /\s+$/, 'lastChild');

  return root.innerHTML;
};

// post-order traversal of either firstChild or lastChild,
// remove empty elements, strip pattern from textNodes
function removeWhitespace(root, pattern, which) {
  var node = root[which];
  while (node && node[which]) {
    node = node[which];
  }
  while (node) {
    if (node.nodeType === 3) {
      node.nodeValue = node.nodeValue.replace(pattern, '');
      if (node.nodeValue.length === 0) {
        node = remove(node)[which];
      } else {
        node = node.parentNode;
      }
    } else if (!node.hasChildNodes()) {
      node = remove(node)[which];
    } else {
      node = node.parentNode;
    }
  }
}

// remove the node from its parent, return the parent
function remove(node) {
  var tmp = node.parentNode;
  tmp.removeChild(node);
  return tmp;
}
