var HttpStatusError = require('./errors/http_status_error');
var messageActions = require('../actions/messages');

// We are using a raw XMLHttpRequest because fetch doesn't expose the xhr
// object on request. We need this object to animate our loading bar. To avoid
// using another library, we're doing this manually. If we see ourselves
// wanting progress bars in many places, we can reassess the best approach.
//
// See: https://github.com/github/fetch/pull/290#issuecomment-193988256
export default function(url, method, formData, onProgress) {
  return new Promise((resolve, reject) => {
    var xhr = new XMLHttpRequest();
    var headers = {
      'X-Requested-With': 'XMLHttpRequest',
      Accept: 'application/json'
    };

    var handleXhrError = (message) => {
      var error = new Error(message);
      messageActions.showError();
      return reject(error);
    };

    xhr.onerror = () => handleXhrError('An network error occurred.');
    xhr.ontimeout = () => handleXhrError('The XMLHttpRequest timed out.');

    xhr.onload = () => {
      if (xhr.status < 200 || xhr.status >= 300) {
        var error = new HttpStatusError(`Response status not within range: ${xhr.status}`, xhr);
        return reject(error);
      }
      return resolve(JSON.parse(xhr.response));
    };

    if (onProgress) {
      xhr.upload.addEventListener('progress', e => {
        onProgress({ loaded: e.loaded, total: e.total });
      });
    }

    xhr.open(method, url, true);
    xhr.withCredentials = true;
    for (let h of Object.keys(headers)) {
      xhr.setRequestHeader(h, headers[h]);
    }
    xhr.send(formData);
  });
}
