exports.sticky = function(node, pinSelector = 'body', endSelector = '', anchorToTop = true) {
  // the container that will scroll
  var pinContainer = document.querySelector(pinSelector);
  var endPoint =  endSelector ? document.querySelector(endSelector) : null;

  if (pinContainer == null) {
    throw new Error('Failed to initialize sticky decorator, could not find scroll container');
  }

  var toggleSticky = function() {
    var nodeRect = node.getBoundingClientRect(); // selector
    var nextRect = node.nextElementSibling ? node.nextElementSibling.getBoundingClientRect() : nodeRect; // editor
    var pinRect = pinContainer.getBoundingClientRect();

    if (anchorToTop) {
      if (endPoint) {
        var endRect = endPoint.getBoundingClientRect();
        var stickyConditional = nextRect.top < pinRect.top + nodeRect.height && endRect.top > pinRect.top;
        node.classList.toggle('is-sticky', stickyConditional);
        pinContainer.classList.toggle('is-sticky', stickyConditional);
      } else {
        node.classList.toggle('is-sticky', nextRect.top < pinRect.top + nodeRect.height);
      }
    } else if (Math.abs(nextRect.top - pinRect.bottom) >= nodeRect.height) {
      node.classList.toggle('is-sticky', nextRect.top > pinRect.bottom);
      pinContainer.classList.toggle('has-sticky-footer', nextRect.top > pinRect.bottom);
    }
  };

  pinContainer.addEventListener('scroll', toggleSticky);
  window.addEventListener('resize', toggleSticky);
  toggleSticky();

  return {
    teardown: () => {
      pinContainer.removeEventListener('scroll', toggleSticky);
      window.removeEventListener('resize', toggleSticky);
    }
  };
};
