var createSocket = require('socket.io-client');

export default function initSocket() {
  var url = `${(app.config.ssl ? 'wss' : 'ws')}://${window.location.host}/`;
  var defaults = {
    transports: ['websocket'],
    reconnectionAttempts: app.config.sockets.reconnectAttempts
  };

  app.socket = createSocket(url, defaults);
  app.contentApiSocket = createSocket(url, Object.assign({}, defaults, {
    path: '/api/content/socket.io/'
  }));
}
