var Emitter = require('eventemitter2').EventEmitter2;
var renderComponent = require('./lib/render_component');
var userActions = require('./actions/users');
var initPolyfills = require('./initializers/00_polyfills');
var initState = require('./initializers/01_state');
var initSocket = require('./initializers/02_socket');
var initConnectionStatus = require('./initializers/connection_status');
var initErrorCatcher = require('./initializers/error_catcher');
var initPreventBackspace = require('./initializers/prevent_backspace');
var initRactive = require('./initializers/ractive');
var initRouter = require('./initializers/router');
var initSentry = require('./initializers/sentry');
var initAnnouncements = require('./initializers/announcements');
var initMessages = require('./initializers/messages');
var initNewUser = require('./initializers/new_user');

var App = window.App = function({ env, config }) {
  Emitter.call(this);
  this.setMaxListeners(0);

  this.env = env;
  this.config = config;
};

Object.assign(App.prototype, Emitter.prototype);

App.prototype.init = async function({ anthemUser, message }) {
  var user = await userActions.getCurrentUser();

  // Run the initializers
  initPolyfills();
  initState(Object.assign(anthemUser, user));
  initSocket();
  initConnectionStatus();
  initErrorCatcher();
  initPreventBackspace();
  initRactive();
  initRouter();
  initSentry();
  initAnnouncements();
  initMessages(message);
  initNewUser();

  this.router.start();
};

App.prototype.render = function(Component, data, target) {
  target = target || document.body;

  // Set new data instead of re-rendering if we're working with the same component type
  var previousRoot = this.rootComponent;
  if (previousRoot instanceof Component) {
    return previousRoot.set(data);
  }

  // Build new component
  this.rootComponent = new Component({ data: data });

  // Render (and teardown previousRoot if it exists)
  var promise = previousRoot ? previousRoot.teardown() : Promise.resolve();
  return promise.then(() => renderComponent(this.rootComponent, target));
};
