require('./initializers/00_polyfills')();

var Baobab = require('baobab');
var initRactive = require('./initializers/ractive');
var renderComponent = require('./lib/render_component');
var res = require('resonance');

var dragging = false;

const OBJECT_TYPES = [
  'actionbox',
  'credits',
  'doc',
  'hr',
  'html',
  'image',
  'newsletter',
  'oembed',
  'pullquote',
  'toh_project_details',
  'toh_tools',
  'pym',
  'ratingcard',
  'readmore',
  'video'
];

function onload() {
  var data = JSON.parse(window.frameElement.dataset.object);
  data.type = Object.keys(data).find(key => OBJECT_TYPES.includes(key));

  // Remove placeholder loading indicator and render data
  document.body.innerHTML = '';
  var InlineObject = require('./components/object/inline-object.html');
  renderComponent(new InlineObject({ data }), document.body);

  res.init({ loadCSSManually: true, shimStyles: true });
}

function generateClick(e) {
  e.preventDefault();
  e.stopPropagation();
  var event = new MouseEvent('click', {
    bubbles: true,
    cancelable: true
  });
  if (!dragging) {
    window.frameElement.dispatchEvent(event);
  }
  return false;
}

// Create a minimal copy of the main app instance for the current object. Clone
// the state tree into a unique instance. This ensures cursor listeners within
// <inline-object> don't try to listen to events fired by the main window, since
// doing so causes errors in Firefox and Edge once the iframe is removed from the
// DOM. The caveat here is that as window.top.app.state updates, our instance of
// app.state won't keep track. For now, that is not a concern since we only care
// about initial snapshots of state.
//
// More details about why we need this work around live here:
// https://github.com/voxmedia/anthem/issues/5747
function App() {
  this.config = window.top.app.config;
  this.env = window.top.app.env;
  this.state = new Baobab(window.top.app.state.deepClone());
  initRactive(this);
}

// Very first thing, setup window.app
window.app = new App();

window.addEventListener('load', onload);
window.addEventListener('click', generateClick);
window.addEventListener('touchend', generateClick);
window.addEventListener('touchmove', () => dragging = true);
window.addEventListener('touchstart', () => dragging = false);
