function _recordPageview(url) {
  if (window.ga) {
    window.ga('send', 'pageview', url);
  }
}

function _recordEvent(action, label, value) {
  if (window.ga) {
    window.ga('send', 'event', action, label, value);
  }
}

module.exports = {
  sendPageview: _recordPageview,
  sendEvent: _recordEvent
};
