export default function resizeObject(win) {
  var frame = win.frameElement;
  if (!frame.hasAttribute('data-preserve-height')) {
    var height = win.document.documentElement.scrollHeight;
    frame.style.height = `${height}px`;
  }

  // Propagate up to parents if subframe
  if (win.parent !== win.top) {
    resizeObject(win.parent);
  }
}
