var moment = require('moment-timezone');
var messages = require('../config/messages');
var userActions = require('../actions/users');
var messageActions = require('../actions/messages');

// TODO: set this threshold based on when AnthemUser ships
const NEW_USER_MARK = moment('2018-03-29', 'YYYY-MM-DD').utc();

export default function initNewUser() {
  var user = app.state.get(['user']);
  var isNewUser = !user.hasLoggedIn && moment(user.createdOn).utc().isAfter(NEW_USER_MARK);

  // If this is the user's first login AND the account was recently created, show
  // them our welcome message
  if (isNewUser) {
    messageActions.showMessage({
      type: 'positive',
      message: messages.NEW_USER_MESSAGE.replace(
        '{url}',
        'https://www.chorus.help/hc/en-us/articles/360009073453-Getting-started-with-the-story-editor'
      )
    });
  }

  // Flag this user as having logged on if they haven't
  if (!user.hasLoggedIn) {
    userActions.updateAnthemUser({ hasLoggedIn: true });
  }
}
