var messageActions = require('../actions/messages');

export default function initMessages(message) {
  if (message && message.message) {
    setTimeout(() => messageActions.showMessage(message), 1000);
  }
  app.socket.on('flash-message', params => {
    messageActions.showMessage(params);

    // Disconnect from servers when we want to force a refresh
    if (params.forceRefresh) {
      app.emit('sockets.disconnect', 'force-refresh');
    }
  });
}
