var { isEqual, omit } = require('lodash');

// Adds a modal to the top of the stack
export function pushModal(data, tail) {
  var current = app.state.get('modal');
  // only push the modal if it's different from what's already at the top of the stack
  if (!isEqual(data, omit(current, 'tail'))) {
    data.tail = tail || current;
    app.state.set('modal', data);
    document.body.classList.add('has-modal');
    if (app.state.get('overlay')) {
      var overlay = document.querySelector('.m-overlay');
      overlay.classList.remove('is-full-screen');
    }
  }
}

// Replaces the modal at the top of the stack with the provided one
export function swapModal(data) {
  var current = app.state.get('modal');
  if (current.tail) {
    data.tail = current.tail;
  }
  app.state.set('modal', data);
}

// Removes modal at the top of the stack
export function popModal() {
  var current = app.state.get('modal');
  if (current.tail) {
    app.state.set('modal', current.tail);
  } else {
    closeModal();
  }
}

export function closeModal() {
  app.state.set('modal', null);
  document.body.classList.remove('has-modal');
  if (app.state.get('overlay')) {
    var overlay = document.querySelector('.m-overlay');
    overlay.classList.add('is-full-screen');
  }
}

export function getModal(key) {
  return app.state.get('modal', key);
}
