var page = require('page');

module.exports = function() {
  // Global middleware
  page('*',
    require('../middleware/analytics'),
    require('../middleware/querystring')
  );

  // Pages that load a doc need some common initialization
  // TODO: refactor routes so there is a common prefix, like:
  // /story/:uuid/editor
  page('/(compose|preview|u-layout|preflight|sass-editor|package|customstory|configure)/(.*)', function(ctx, next) {
    require(['../middleware/load_doc'], function(m) { m(ctx, next); });
  });

  // Editor-like things need editor-like behaviors first.
  page('/compose/(.*)', function(ctx, next) {
    require(['../middleware/render_compose'], function(m) { m(ctx, next); });
  });
  page('/compose/:id/diff/:baseId/:targetId', function(ctx) {
    require(['../routes/diff'], action => action(ctx));
  });
  page('/compose/:id/version/:version', function(ctx) {
    require(['../routes/version'], action => action(ctx));
  });
  page('/compose/:id', function(ctx) {
    require(['../routes/compose'], action => action(ctx));
  });
  page('/configure/:id', function(ctx) {
    require(['../routes/configure'], action => action(ctx));
  });
  page('/package/:id', function(ctx) {
    require(['../routes/package'], action => action(ctx));
  });
  page('/preview/:id', function(ctx) {
    require(['../routes/preview'], action => action(ctx));
  });
  page('/u-layout/:id', function(ctx) {
    require(['../routes/u-layout'], action => action(ctx));
  });
  page('/preflight/:id', function(ctx) {
    require(['../routes/preflight'], action => action(ctx));
  });
  page('/sass-editor/:id', function(ctx) {
    require(['../routes/sass_editor'], action => action(ctx));
  });
  page('/customstory/:id', function (ctx) {
    require(['../routes/customstory'], action => action(ctx));
  });
  page('*', function(ctx) {
    require(['../routes/notfound'], action => action(ctx));
  });

  app.router = page;
};
