var createHash = require('sha.js').bind(null, 'sha1');
var createHmac = require('hmac').bind(null, createHash, 64);

module.exports = function(source, options) {
  var url = generateUrl(source, options);
  var signature = createHmac(options.config.key)
    .update(url)
    .digest('base64')
    .replace(/\+/g, '-')
    .replace(/\//g, '_');
  return options.config.host + '/' + signature + '/' + url;
};

function generateUrl(source, options) {
  var urlParts = [];

  if (options.meta) {
    urlParts.push('meta');
  }

  if (options.trim) {
    var trimOptions = ['trim'];
    if (options.trim !== true) {
      trimOptions.push(options.trim);
    }
    urlParts.push(trimOptions.join(':'));
  }

  options.crop = options.crop || calculateCenteredCrop(options);

  if (options.crop) {
    var cropLeft = options.crop[0];
    var cropTop = options.crop[1];
    var cropRight = options.crop[2];
    var cropBottom = options.crop[3];

    if (cropLeft > 0 || cropTop > 0 || cropBottom > 0 || cropRight > 0) {
      urlParts.push(cropLeft + 'x' + cropTop + ':' + cropRight + 'x' + cropBottom);
    }
  }

  for (let fit of [
    'fit_in',
    'adaptive_fit_in',
    'full_fit_in',
    'adaptive_full_fit_in'
  ]) {
    if (options[fit]) {
      urlParts.push(fit.toString().replace(/_/g, '-'));
    }
  }

  if ((options.fit_in || options.full_fit_in) && !(options.width || options.height)) {
    throw new Error('When using fit_in or full_fit_in, you must specify width and/or height');
  }

  var size = calculateSize(options);

  if (size) {
    urlParts.push(size);
  }

  if (options.halign && options.halign !== 'center') {
    urlParts.push(options.halign);
  }

  if (options.valign && options.valign !== 'middle') {
    urlParts.push(options.valign);
  }

  if (options.smart) {
    urlParts.push('smart');
  }

  if (options.filters && options.filters.length) {
    urlParts.push('filters:' + options.filters.join(':'));
  }

  urlParts.push(source.replace(/https?:\/\//, ''));

  return urlParts.join('/');
}

function calculateCenteredCrop(options) {
  var width = options.width;
  var height = options.height;
  var originalWidth = options.original_width;
  var originalHeight = options.original_height;
  var center = options.center;

  if (!originalWidth ||
      !originalHeight ||
      !center ||
      !(width || height)) {
    return;
  }

  if (!Array.isArray(center) || center.length !== 2) {
    throw new Error('center must be an array of x,y');
  }

  width = Math.abs(width || originalWidth);
  height = Math.abs(height || originalHeight);

  var centerX = center[0];
  var centerY = center[1];
  var newAspectRatio = width / height;
  var originalAspectRatio = originalWidth / originalHeight;

  // We're going wider, vertical crop
  if (newAspectRatio > originalAspectRatio) {
    // How tall should we be? because newAspectRatio is > originalAspectRatio we can be sure
    // that croppedHeight is always less than originalHeight (original). This is assumed below.
    var croppedHeight = Math.round(originalWidth / newAspectRatio);

    // Calculate coordinates around center
    var topCropPoint = Math.round(centerY - (croppedHeight * 0.5));
    var bottomCropPoint = Math.round(centerY + (croppedHeight * 0.5));

    // If we've gone above the top of the image, take all from the bottom
    if (topCropPoint < 0) {
      topCropPoint = 0;
      bottomCropPoint = croppedHeight;
    // If we've gone below the top of the image, take all from the top
    } else if (bottomCropPoint > originalHeight) {
      topCropPoint = originalHeight - croppedHeight;
      bottomCropPoint = originalHeight;
      // Because croppedHeight < originalHeight, topCropPoint and
      // bottomCropPoint will never both be out of bounds
    }

    // Put it together
    return [0, topCropPoint, originalWidth, bottomCropPoint];
  // We're going taller, horizontal crop
  } else if (newAspectRatio < originalAspectRatio) {
    // How wide should we be? because newAspectRatio is < originalAspectRatio we can be sure
    // that croppedWidth is always less than originalWidth (original). This is assumed below.
    var croppedWidth = Math.round(originalHeight * newAspectRatio);

    // Calculate coordinates around center
    var leftCropPoint = Math.round(centerX - (croppedWidth * 0.5));
    var rightCropPoint = Math.round(centerX + (croppedWidth * 0.5));

    // If we've gone beyond the left of the image, take all from the right
    if (leftCropPoint < 0) {
      leftCropPoint = 0;
      rightCropPoint = croppedWidth;
    // If we've gone beyond the right of the image, take all from the left
    } else if (rightCropPoint > originalWidth) {
      leftCropPoint = originalWidth - croppedWidth;
      rightCropPoint = originalWidth;
      // Because croppedWidth < originalWidth, leftCropPoint and
      // rightCropPoint will never both be out of bounds
    }

    // Put it together
    return [leftCropPoint, 0, rightCropPoint, originalHeight];
  }
}

function calculateSize(options) {
  var width = options.width;
  var height = options.height;

  if (width && options.flip) { width *= -1; }
  if (height && options.flop) { height *= -1; }

  if (width || height) {
    if (!width) { width = 0; }
    if (!height) { height = 0; }
  }

  var hasWidth = (width != null);
  var hasHeight = (height != null);

  if (options.flip && !hasWidth && !hasHeight) {
    width = '-0';
    if (!hasHeight && !options.flop) { height = '0'; }
  }

  if (options.flop && !hasWidth && !hasHeight) {
    height = '-0';
    if (!hasWidth && !options.flip) { width = '0'; }
  }

  if (width || height) {
    return width + 'x' + height;
  }
}
