var Ractive = require('ractive');

require('../vendor/ractive-events-keys');

module.exports = function() {
  Object.assign(Ractive.defaults, {
    debug: true,
    isolated: true,
    modifyArrays: false,
    once: function(event, callback) {
      var self = this;
      var wrapped = function() {
        self.off(event, wrapped);
        callback.apply(self, arguments);
      };
      self.on(event, wrapped);
    },
    listenTo: function(obj, name, callback, useCapture) {
      callback = callback.bind(this);
      (obj.on || obj.addEventListener).call(obj, name, callback, useCapture);
      this.on('teardown', function() {
        (obj.off || obj.removeEventListener).call(obj, name, callback, useCapture);
      });
      return this;
    },
    listenToOnce: function(obj, name, callback) {
      callback = callback.bind(this);
      obj.once(name, callback);
      this.on('teardown', function() {
        obj.off(name, callback);
      });
      return this;
    }
  });

  Object.assign(Ractive.decorators,
    require('../lib/ractive-decorator-playgif'),
    require('../lib/ractive-decorator-autosize'),
    require('../lib/ractive-decorator-sticky'),
    require('../lib/ractive-decorator-activefield')
  );

  Ractive.defaults.onconstruct = function(opts) {
    if (this.cursors == null) { return; }

    var watcher = app.state.watch(this.cursors);

    opts.data = opts.data || {};
    Object.assign(opts.data, watcher.get());

    watcher.on('update', () => this.set(watcher.get()));
  };

  Ractive.defaults.syncCursor = function(key) {
    var cursor = this.cursors[key];
    if (!cursor) {
      throw new Error('unknown cursor for key: ' + key);
    }
    cursor = app.state.select(cursor);
    this.observe(key, val => cursor.set(val), { init: false });
  };

  Ractive.defaults.refreshIframeComponent = function(paths, iframeKey) {
    this.observe(paths, newValue => {
      if (newValue) {
        var iframe = this.find(iframeKey);
        if (iframe) {
          iframe.src = iframe.src;
        }
      }
    });
  };
};
