const { throttle, set: setPath } = require('lodash');
const { dynamicNode } = require('baobab');
const uuid = require('uuid');
const request = require('../lib/request');
const OT = require('../lib/anthem0');
const messages = require('../config/messages');
const initPresence = require('../initializers/editor/presence');
const setBaseContents = require('../initializers/defaults/base_contents');
const initSubdocs = require('../initializers/editor/doc/subdocs');
const messageActions = require('./messages');
const sentryActions = require('./sentry');
const hash = require('../../shared/content-hash');
const WorkerQueue = require('../lib/worker_queue');

// State variables and cursors
var storage = window.localStorage;
var fatalError = false;

var config = app.config;
var userCursor = app.state.select('user');
var dirty = app.state.select(['autosave', 'dirty']);
var docsById = app.state.select(['autosave', 'docsById']);
var inLine = app.state.select(['autosave', 'inLine']);
var inFlight = app.state.select(['autosave', 'inFlight']);
var sentIds = app.state.select(['autosave', 'sentIds']);
var quillContentPaths = app.state.select(['autosave', 'quills']);
var reconnectingIds = app.state.select(['autosave', 'reconnectingIds']);
var initializingIds = app.state.select(['autosave', 'initializingIds']);

const MESSAGE_RESCUE_VERSION = ['story'];

// ##### Helper Methods #####

function rescueKey(docId) {
  return `rescue:${docId}`;
}

function messageKey(docId) {
  return `message:${docId}`;
}

function getDocIds() {
  return Object.keys(docsById.get()).map((docId) => parseInt(docId));
}

// Each autosave packet must contain a docId that is used to find the docs
// reference in state. If no reference is found, shut down autosave before
// anything else can go amiss.
function validateId(docId) {
  if (!docInState(docId)) {
    var err = new Error('An invalid docId was supplied to autosave.');
    err.name = 'AutosaveReferenceError';
    err.doc_id = docId;
    markError(err);
  }
}

// ##### Markers #####
// Set the current state of the autosave pipeline

function markDirty(docId) {
  dirty.set(docId, true);
  app.emit(`autosave.dirty.${docId}`, inLine.get(docId), inFlight.get(docId));
}

function markClean(docId) {
  dirty.unset(docId);
  app.emit(`autosave.clean.${docId}`);
}

function markError(err, reportToSentry = true) {
  fatalError = true;
  err.from = 'autosave';
  dirty.set({});
  app.emit('autosave.error', err);
  if (reportToSentry) {
    sentryActions.reportError(err);
  }
}

// ##### Client Update #####
// Applies the update to the client-side state. Either before sending (for local)
// or on receiving a packet from the server

function applyDelta(docId, delta) {
  var deltaCursor = getDocCursor(docId).select(['head', 'delta']);
  deltaCursor.apply(base => OT.compose(base, delta));

  var contentsCursor = getDocCursor(docId).select(['head', 'contents']);
  contentsCursor.apply(contents => OT.apply(contents, delta));
}

// ##### Request Logic #####
// Sends autosave requests, handles initial error parsing (including retry flow)

// Throttled function that, when executed, sends updates for all inline docs
var sendIfReady = throttle(function sendIfReady() {
  for (let docId of getDocIds()) {
    if (!inFlight.get(docId) && inLine.get(docId) && !fatalError) {
      inFlight.set([docId], inLine.get(docId));
      inLine.unset(docId);
      storeRescueVersion(docId);
      sendUpdate(docId);
    }
  }
}, config.autosaveThrottle, { leading: false, trailing: true });

// Called by `sendIfReady` to actuall send inLine data as a single packet
function sendUpdate(docId) {
  // If an error has occurred prior, don't send
  if (fatalError) {
    return;
  }

  // If we have anything else waiting, need to send that too
  // in order for contentHash to be accurate
  if (inLine.get(docId)) {
    try {
      var composed = OT.compose(inFlight.get([docId, 'delta']), inLine.get([docId, 'delta']));
      inFlight.set([docId, 'delta'], composed);
      inLine.unset(docId);
      storeRescueVersion(docId);
    } catch (error) {
      error.name = 'OTError';
      markError(error);
      return;
    }
  }

  var docCursor = getDocCursor(docId);
  var sequenceCursor = getDocCursor(docId).select(['head', 'end_sequence']);
  var contentsCursor = getDocCursor(docId).select(['head', 'contents']);
  var delta = inFlight.get([docId, 'delta']);
  var packetUuid = uuid.v4();
  var sequence = sequenceCursor.get() + 1;

  sentIds.set(packetUuid, true);

  return request('/api/content/docs/' + docCursor.get('id') + '/contents', {
    method: 'PATCH',
    body: {
      user_id: inFlight.get([docId, 'userId']),
      delta,
      sequence,
      uuid: packetUuid,
      content_hash: hash(contentsCursor.get()),
      schema_version: docCursor.get('schema_version'),
    },
    opts: {
      handleError: false,
      retry: {
        delay: 0,
        maxDelay: 20000
      }
    }
  }).then(() => {
    app.emit(`autosave.success.${docId}`, inFlight.get(docId));
    storage.removeItem(rescueKey(docId));
    inFlight.unset(docId);
    sequenceCursor.set(sequence);
    if (inLine.get(docId)) {
      markDirty(docId);
    } else {
      markClean(docId);
    }
    sendIfReady();
  }).catch(err => {
    var status = err.response && err.response.status;
    if (status === 409) {
      // the rebase case, call remoteUpdate on all the packets to catch up then try again
      return retryAfterRebase(docId, err);
    } else {
      // reject to let the next catch block handle the error
      return Promise.reject(err);
    }
  }).catch(err => {
    // some other status code or non-network error happened, shut down the
    // editor and save the latest rescue version
    markError(err, false);
    setTimeout(() => saveRescueVersion(docId, false), 5000);
  });
}

// If we get a 409 conflict error, do:
// A) Rebase and retry if we've missed data
// B) Shut it down if it's any other kind of conflict error (i.e. too far ahead)
function retryAfterRebase(docId, err) {
  // must clone the response so that reportError can also read the body,
  // should this fail
  var response = err.response.clone();
  return response.json().then(function(data) {
    if (/too low/.test(data.message) && data.packets) {
      for (let packet of data.packets) {
        remoteUpdate(docId, packet);
      }
      return sendUpdate(docId);
    } else {
      return Promise.reject(err);
    }
  });
}

// ##### Rescue Logic #####

// Saves a rescue version locally. Used as a precaution when sending and for
// reference on a true error
function storeRescueVersion(docId) {
  app.emit('autosave.check-update', docId);

  var delta = [];
  var contents = getDocCursor(docId).get(['head', 'contents']);

  try {
    if (inFlight.get(docId)) {
      delta = OT.compose(delta, inFlight.get([docId, 'delta']));
    }
    if (inLine.get(docId)) {
      delta = OT.compose(delta, inLine.get([docId, 'delta']));
    }
    if (window.quills[docId]) {
      var quill = window.quills[docId];
      var path = quillContentPaths.get([docId, 'path']);
      if (quill && path) {
        setPath(contents, path, quill.getContents());
      }
    }
  } catch (error) {
    error.name = 'OTError';
    markError(error);
    return;
  }

  var docCursor = getDocCursor(docId);
  storage.setItem(rescueKey(docId), JSON.stringify({
    doc_id: docCursor.get('id'),
    user_id: userCursor.get('id'),
    schema_version: docCursor.get('schema_version'),
    schema_name: docCursor.get('schema_name'),
    end_sequence: null,
    contents: contents,
    delta: delta
  }));
}

// On a true error, try to save a rescue version server-side
function saveRescueVersion(docId, flashNow) {
  // Don't save a rescue version if one doesn't exist (i.e. due to a local error)
  var version = storage.getItem(rescueKey(docId));
  if (!version) {
    return;
  }

  return request('/api/content/versions', {
    method: 'POST',
    body: JSON.parse(version),
    opts: { handleError: false }
  }).then(resp => {
    var isPackageDashboard = window.location.pathname.indexOf('package') > -1;
    if (!isPackageDashboard && MESSAGE_RESCUE_VERSION.indexOf(resp.schema_name) > -1) {
      var docUuid = getDocCursor(docId).get('uuid');
      var message = {
        message: messages.RESCUE_SAVED_REASSURE.replace(
          '{url}',
          `/compose/${docUuid}/version/${resp.id}`
        ),
        type: 'cautionary'
      };

      // If we are saving this rescue version on a page reload, flash a message
      // and report to Sentry. Otherwise don't, as it's already been handled.
      if (flashNow) {
        messageActions.showMessage(message);
      } else {
        setNextFlash(docId, message);
      }
    }
    storage.removeItem(rescueKey(docId));
  }).catch(err => {
    markError(err, false);
  });
}

// When called, checks to see if a rescue version is saved in storage for the
// given doc and saves it to the server if it exists
function checkForRescue(docId) {
  setTimeout(() => getNextFlash(docId), 1000);
  if (storage.hasOwnProperty(rescueKey(docId))) {
    // Allow error handler to be registered before emitting
    setTimeout(() => saveRescueVersion(docId, false), 500);
  }
}

function setNextFlash(docId, message) {
  storage.setItem(messageKey(docId), JSON.stringify(message));
}

function getNextFlash(docId) {
  var key = messageKey(docId);
  if (storage.hasOwnProperty(key)) {
    var message = JSON.parse(storage.getItem(key));
    messageActions.showMessage(message);
    storage.removeItem(key);
  }
}

// Remove now obsolete `legacy_updated_at` publish history event if the first
// local update is being made for a synced legacy story
function removeLegacyPubEvent(publishHistory, delta) {
  if (publishHistory && publishHistory.length > 0) {
    var legacyPubEventIndex = publishHistory.findIndex(e => e.event_type === 'legacy_updated_at');
    if (legacyPubEventIndex > -1) {
      var legacyPubEvent = publishHistory[legacyPubEventIndex];
      delta.push({
        p: ['publish_history', legacyPubEventIndex],
        ld: legacyPubEvent
      });
      return delta;
    }
  }
  return delta;
}

// ##### Exposed Action Functions #####

/**
 * Called to get the doc cursor by docId
 * @param {Number} docId A valid docId
 */
export function getDocCursor(docId) {
  validateId(docId);
  return docsById.select(docId);
}

/**
 * Called to get a doc in state
 * pipeline
 * @param {Number} docId
 */
export function docInState(docId) {
  return docId && docsById.get(docId);
}

/**
 * Called to get the dirty state for a specific doc by its id OR the whole
 * pipeline
 * @param {Number} optional docId
 */
export function isDirty(docId = null) {
  var dirtyState = dirty.get();
  return docId ? dirtyState[docId] : Object.keys(dirtyState).length > 0;
}

/**
 * Called to set a doc in state for autosave tracking and editing
 * @param {Object} doc A valid doc set in state
 * @param {String} path The path at which to set the doc
 */
export function setDoc(doc, path) {
  docsById.set(doc.id, doc);

  if (path) {
    app.state.set(path, dynamicNode({
      cursors: {
        doc: ['autosave', 'docsById', doc.id]
      },
      get: ({ doc }) => doc
    }));
  }

  return Promise.resolve();
}

/**
 * Called to initalize a doc for autosave
 * @param {Object} docId A valid doc id
 * @param {Boolean} greetUsers Whether to update the cursor for user presence
 */
export function initDoc(docId, greetUsers = false) {
  // autosave should be set up, just check for rescue version for this doc
  checkForRescue(docId);

  initPresence(docId.toString(), greetUsers);

  initSubdocs(docId);

  return setBaseContents(docId);
}

/**
 * Called to set a doc in state and initalize it for autosave
 * @param {Number} id A valid doc id or uuid to initialize
 * @param {Boolean} optional greetUsers Whether to update the cursor for user presence
 * @param {String} optional path The path at which to set the doc
 */
export function setAndInitDoc(doc, greetUsers = false, path = null) {
  // If the doc is already in state, do nothing
  if (docInState(doc.id) || initializingIds.get(doc.id)) {
    return Promise.resolve();
  }

  initializingIds.set(doc.id, true);

  return setDoc(doc, path)
    .then(() => initDoc(doc.id, greetUsers))
    .then(() => initializingIds.unset(doc.id));
}

/**
 * Called to unset a doc in state after editing
 * @param {Object} path The path at which to unset the doc
 */
export function unsetDocAlias(path) {
  return app.state.unset(path);
}

/**
 * Called to remove a doc from state for autosave tracking
 * @param {Number} docId A valid docId to remove from state
 */
export function teardownDoc(docId) {
  if (!docInState(docId)) {
    return Promise.resolve();
  }

  return resolveOnClean(docId).then(() => {
    docsById.unset(docId);

    // if the doc was initialized with greetUsers set to false,
    // this presence.leave just tears down the socket.io room for
    // receiving remote updates
    app.socket.emit(`presence.${docId}.leave`);
    app.contentApiSocket.emit(`presence.${docId}.leave`);
  });
}

/**
 * Called to send a client-side update to the server
 * @param {Number} docId A valid docId for which the update applies
 * @param {Delta} delta A valid rich-text delta to be sent as part of the update
 * @param {Number} [userId] A userId value for the packet to override the current user
 */
export function localUpdate(docId, delta, userId) {
  validateId(docId);
  userId = userId || userCursor.get('id');

  var docCursor = getDocCursor(docId);
  docCursor.set('updated_at', new Date().toISOString());
  if (!inLine.get(docId)) {
    inLine.set(docId, { delta: [], userId: userId });
  } else {
    inLine.set([docId, 'userId'], userId);
  }

  var publishHistory = docCursor.get(['head', 'contents', 'publish_history']);
  delta = removeLegacyPubEvent(publishHistory, delta);

  try {
    inLine.set([docId, 'delta'], OT.compose(inLine.get([docId, 'delta']), delta));
    storeRescueVersion(docId);
    applyDelta(docId, delta);
    markDirty(docId);
    app.emit('autosave.local-update', docId, delta);
    sendIfReady();
  } catch (error) {
    error.name = 'OTError';
    markError(error);
  }
}

export function resolveOnLocalUpdate(docId, delta, userId) {
  localUpdate(docId, delta, userId);
  return resolveOnClean(docId);
}

// if true, remote updates will be queued up until ready to apply them
let _shouldQueueRemoteUpdates = false;
const _remoteUpdateQueue = new WorkerQueue();

/**
 * Called to parse a remote update from the server. Hooked up to an event listener
 * in client/initializers/editor/autosave
 * @param {Number} docId A valid docId for which the update applies
 * @param {Object} packet The server side packet
 * @param {Delta} packet.delta The update itself
 */
export function remoteUpdate(docId, packet) {
  if (_shouldQueueRemoteUpdates) {
    _remoteUpdateQueue.push(applyRemoteUpdate, docId, packet);
  } else {
    applyRemoteUpdate(docId, packet);
  }
}

async function pullMissingPackets(docId, sequence) {
  // start queuing any remote updates that happen from now on so we can apply
  // any missing ones first
  _shouldQueueRemoteUpdates = true;
  const ops = await fetchNewPackets(docId, sequence);

  // validate that the ops we got are applicable
  if (!ops.length) {
    throw new Error('Unable to get missing sequences');
  }

  // abort if the sequences aren't sequential so we don't get stuck trying to do
  // this over and over again recursively
  const sequences = ops.map(op => op.sequence);
  const isSequential = (sequence, index) => (index === 0 || sequence - sequences[index - 1] === 1);
  const areSequential = sequences.every(isSequential);
  if (sequences[0] > sequence + 1 || !areSequential) {
    throw new Error(`Still missing sequences after fetching. Current: ${sequence}, Got: ${sequences}`);
  }

  // apply each op sequentially
  for (let op of ops) {
    await applyRemoteUpdate(docId, op);
  }

  // stop queuing and process any we got while we were pulling
  _shouldQueueRemoteUpdates = false;
  _remoteUpdateQueue.process();
}

async function applyRemoteUpdate(docId, packet) {
  // If the docId is invalid, simply don't apply the update
  if (fatalError || !docInState(docId)) {
    return;
  }

  // Ignore packets that we sent
  if (sentIds.get(packet.uuid)) {
    sentIds.unset(packet.uuid);
    return;
  }

  const sequenceCursor = getDocCursor(docId).select(['head', 'end_sequence']);
  const sequence = sequenceCursor.get();

  // Ignore packets we've already applied
  if (packet.sequence <= sequence) {
    return;
  }

  // If we missed a sequence, get all the sequences since the one we have
  if (packet.sequence > sequence + 1) {
    try {
      await pullMissingPackets(docId, sequence);
    } catch (err) {
      err.original_name = err.name;
      err.name = `AutosaveSequenceError`;
      err.doc_id = docId;
      err.missed_packets = packet.sequence - (sequenceCursor.get() + 1);

      markError(err);
    }

    return;
  }

  // Make sure we have the latest contents recorded for all active docs
  app.emit('autosave.check-update', docId);

  try {
    var left;
    var right;

    if (inFlight.get(docId)) {
      left = OT.transform(packet.delta, inFlight.get([docId, 'delta']), 'left');
      right = OT.transform(inFlight.get([docId, 'delta']), packet.delta, 'right');
      packet.delta = left;
      inFlight.set([docId, 'delta'], right);
    }

    if (inLine.get(docId)) {
      left = OT.transform(packet.delta, inLine.get([docId, 'delta']), 'left');
      right = OT.transform(inLine.get([docId, 'delta']), packet.delta, 'right');
      packet.delta = left;
      inLine.set([docId, 'delta'], right);
    }

    sequenceCursor.set(packet.sequence);
    applyDelta(docId, packet.delta);
  } catch (err) {
    err.name = 'OTError';
    markError(err);
  }

  app.emit('autosave.remote-update', docId, packet);
}

/**
 * Updates top-level doc values as they are emitted from the server. Currently used
 * for status and trashed_at updates. Hooked up to an event listener in the autosave initializer
 * @param {Number} docId A valid docId for which the update applies
 * @param {Array} path A path for the value
 * @param {Any} value The value to be set
 */
export function updateDocFlag(docId, path, value) {
  // If the docId is invalid, simply don't apply the update
  if (!docInState(docId)) {
    return;
  }

  var doc = getDocCursor(docId);
  doc.set(path, value);
}

/**
 * Creates a Promise that resolves when the current pipeline is marked clean
 */
export function resolveOnClean(docId) {
  return new Promise((resolve, reject) => {
    if (!isDirty(docId)) {
      return resolve();
    }
    app.once(`autosave.clean.${docId}`, resolve);
    app.once('autosave.error', reject);
  });
}

// retrieves all packets greater than {sequence}
function fetchNewPackets(docId, sequence) {
  return request(`/api/content/docs/${docId}/ops?sequence__gt=${sequence}`, {
    opts: {
      retry: {
        delay: 0,
        maxDelay: 20000
      }
    }
  });
}

export function onReconnect() {
  for (let docId of getDocIds()) {
    if (inFlight.get(docId) || reconnectingIds.get(docId)) {
      // if there is an autosave in progress for this doc,
      // we will get any ops we missed as part of a 409 response (if applicable)
      continue;
    }

    reconnectingIds.set(docId, true);
    var sequence = getDocCursor(docId).get(['head', 'end_sequence']);

    fetchNewPackets(docId, sequence).then(function(ops) {
      for (let op of ops) {
        remoteUpdate(docId, op);
      }
      reconnectingIds.unset(docId);
    }).catch(err => {
      markError(err, false);
    });
  }
}
