const { omit, merge } = require('lodash');
const { Delta } = require('rich-text');
const request = require('../lib/request');
const { hasPolicyRole } = require('../lib/authorization');
const modalActions = require('./modals');
const autosave = require('./autosave');
const editorActions = require('./editor');
const assetActions = require('./assets');

const ID_FROM_CDN_URL_PATTERN = /chorus_(asset|image)\/(file|image)\/([\d]+)\//;
export const HAS_IMAGE_HISTORY = ['story', 'gallery'];

function setCropData(image) {
  if (!image.subject && !image.crop) {
    image.crop = { x: 0, y: 0, w: image.width, h: image.height };
    image.subject = { x: Math.round(image.width / 2), y: Math.round(image.height / 2) };
  }
  return image;
}

// If we're building an image with no existing source info (i.e. a wire image),
// then use along the current user and community data.
function setSourceData(image) {
  image.network_id   = image.network_id || app.state.get(['community', 'network', 'id']);
  image.community_id = image.community_id || app.state.get(['community', 'id']);
  image.user_id      = image.user_id || app.state.get(['user', 'id']);
  return image;
}

export function getImage(id) {
  return request('/api/assets/images/' + id);
}

function getChorusImage(id) {
  return request(`/api/chorus/images/${id}`);
}

export function uploadImageUrl(type, target = {}) {
  modalActions.pushModal({ uploadImageUrl: { type, target } });
}

export function updateUploadProgress(progress) {
  app.state.set(['uploadProgress'], progress);
}

export function selectImage(imageType, target = {}) {
  modalActions.pushModal({ selectAsset: { imageType, target, type: 'image' } });
}

export function editImage(image, label, type, target = {}) {
  let data = setSourceData(setCropData(Object.assign({}, image)));
  let editObject = { editObject: { data, label, type, target } };
  // if selectAsset modal already exists or use does not have access to assets
  if (modalActions.getModal('selectAsset') || !hasPolicyRole('editor.accessAssets')) {
    modalActions.pushModal(editObject);
  } else {
    // insert the selectImage modal in between the current modal and the editImage modal
    modalActions.pushModal(editObject, {
      selectAsset: { target, type: 'image', imageType: type },
      tail: app.state.get('modal')
    });
  }
}

export async function getAndEditImage(localImage, type, target = {}) {
  let image = await getImage(localImage.id);
  editImage(merge({}, image, localImage), 'Update', type, target);
}

export function saveImage(image) {
  return assetActions.saveAsset('image', image).then(result => {
    return Object.assign({}, image, omit(result, 'caption'));
  });
}

export function uploadImages(params, onProgress, flash = true) {
  return assetActions.uploadAssets('image', params, onProgress, flash);
}

export function uploadImage(params, onProgress, flash = true) {
  return assetActions.uploadAsset('image', params, onProgress, flash);
}

// If the provided url is a Vox CDN url, try to find the ChorusAsset record
// for the url
export async function getImageFromUrl(url) {
  if (assetActions.validateImageUrl(url) && /vox-cdn/.test(url) && ID_FROM_CDN_URL_PATTERN.test(url)) {
    let match = url.match(ID_FROM_CDN_URL_PATTERN);
    let idFromUrl = parseInt(match[3]);
    let assetId = idFromUrl;

    // If the CDN url is for a ChorusImage, get that record's ChorusAsset id,
    // which may be `null` for older records
    if (match[1] === 'image') {
      let chorusImage = await getChorusImage(idFromUrl);
      assetId = chorusImage.chorus_asset_id;
    }

    // Get the ChorusAsset for the CDN url if we've found a valid ID
    if (assetId) {
      return getImage(assetId);
    }
  }

  return null;
}

export function imageFromData(data) {
  var image = {
    id: data.id,
    src: data.url,
    width: data.width,
    height: data.height,
    crop: data.crop,
    subject: data.subject,
    caption: data.caption || '',
    credit: data.credit || '',
    hide_credit: data.hide_credit != null ? data.hide_credit : false,
    letterbox: data.letterbox != null ? data.letterbox : false,
    mask_text: data.mask_text
  };
  return setCropData(image);
}

export function insertImageAtPath({ docId, path }, data) {
  var image = imageFromData(data);
  var oldImage = autosave.getDocCursor(docId).get(['head', 'contents'].concat(path));

  var delta = [{ p: path, od: oldImage, oi: image }];
  delta = delta.concat(buildImageHistoryDelta(docId, image.id));
  return autosave.resolveOnLocalUpdate(docId, delta);
}

export function removeImageAtPath({ docId, path }, image) {
  return autosave.resolveOnLocalUpdate(docId, [{ p: path, od: image }]);
}

export function insertInlineImage({ docId, lineId }, data) {
  updateImageHistory(docId, data.id);
  return editorActions.insertObject(docId, lineId, { image: imageFromData(data) });
}

export function insertInlineImageAtMouseEvent(docId, event, data) {
  var delta = new Delta().insert({ image: imageFromData(data) });

  updateImageHistory(docId, data.id);
  return editorActions.insertAtMouseEvent(docId, event, delta);
}

export function buildImageHistoryDelta(docId, assetId) {
  var doc = autosave.getDocCursor(docId);
  var schemaName = doc.get(['schema_name']);
  var imageHistory = doc.get(['head', 'contents', 'image_history']) || [];
  var addToHistory = (
    HAS_IMAGE_HISTORY.includes(schemaName) &&
    !imageHistory.find(id => id === assetId)
  );

  return addToHistory
    ? [{ p: ['image_history', imageHistory.length], li: assetId }]
    : [];
}

function updateImageHistory(docId, assetId) {
  var delta = buildImageHistoryDelta(docId, assetId);
  if (delta.length > 0) {
    autosave.localUpdate(docId, delta);
  }
}

export function validatePartnerPlatformsImageSize(bytes, maxSize) {
  // maxSize in MB
  return parseFloat(bytes / 1000000) < maxSize;
}
