module.exports = {
  type: 'line',
  reverseMerge: true,
  add: function(node) {
    node.classList.add('soft-break');
    return node;
  },
  remove: function(node) {
    node.classList.remove('soft-break');
    return node;
  },
  value: function(node) {
    return node.classList.contains('soft-break');
  },
  match: function(node) {
    return node.classList.contains('soft-break');
  }
};
