var autosave = require('../../actions/autosave');
var contentsDefaults = require('./contents_defaults');
var convertObjectFormat = require('./convert_object_format');

export default function(docId) {
  var doc = autosave.getDocCursor(docId).get();
  var contents = doc.head.contents;
  var defaults = contentsDefaults(doc);
  var delta = [];

  for (let [key, value] of Object.entries(defaults)) {
    if (contents[key] == null && value != null) {
      delta.push({ p: [key], oi: value });
    }
  }

  // Set defaults on nested draft object
  if (defaults.draft) {
    if (contents.draft) {
      for (let [key, value] of Object.entries(defaults.draft)) {
        if (contents.draft[key] == null && value != null) {
          delta.push({ p: ['draft', key], oi: value });
        }
      }
    } else {
      delta.push({ p: ['draft'], oi: defaults.draft });
    }
  }

  delta = delta.concat(convertObjectFormat(contents));

  return delta.length > 0 ? autosave.resolveOnLocalUpdate(doc.id, delta, -1) : Promise.resolve();
}
