/*
 * Play GIF
 *
 * by Jesse Young
 * un-jQueryfied by Blake Thomson
 *
 * Play GIF animations on mouseover (or any other event).
 *
 * Examples:
 *    playGIF(document.querySelectorAll('img[src$=".gif"]'));
 *    playGIF(images, { on: 'click' }); // default is mouseover
 */

module.exports = function(elements, options = {}) {
  var settings = Object.assign({
    on: 'mouseenter',  // event to play animation
    off: 'mouseleave',  // event to stop animation
    autoResize: true,  // canvas dimensions adapt to container
    overlay: false,
    circleBackgroundColor: '#33C7E0',
    circleTextColor: '#fff',
    gifText: 'GIF',
    removeAnchor: true // sometimes writers link the GIF to itself, causing it to open in a new window.
  }, options);

  // Draw "GIF" circle on top of image
  var drawGIFOverlay = function(context, x, y) {
    x = Math.floor(x);
    y = Math.floor(y);

    // circle
    var radius = 35;
    context.fillStyle = settings.circleBackgroundColor;
    context.beginPath();
    context.arc(x, y, radius, 0, Math.PI * 2, true);
    context.closePath();
    context.fill();

    // text
    context.fillStyle = settings.circleTextColor;
    context.font = 'sans-serif';
    context.textAlign = 'center';
    context.textBaseline = 'middle';
    context.fillText(settings.gifText, x, y);
  };

  for (let gif of elements) {
    // Skip if it's not a GIF
    if (gif.tagName !== 'IMG' || !/\.gif$/i.test(gif.src)) { return true; }

    var canvas = document.createElement('canvas');
    var context = canvas.getContext('2d');
    var img = new Image();

    var startAnimation = function(a, b) {
      // Hackery to make GIFs play from the beginning everytime.
      // Momentarily replace the src with a blank 1x1 GIF, then set it back
      // to the original src.
      var src = b.src;
      b.src = 'data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==';
      b.style.width = a.width;
      b.style.height = a.height;

      setTimeout(function() { b.src = src; }, 0);

      a.parentNode.replaceChild(b, a);
    };

    var stopAnimation = function(a, b) {
      b.parentNode.replaceChild(a, b);
    };

    img.onload = function() {
      // Replace GIF w/ static canvas image
      var width = gif.offsetWidth;
      var height = gif.offsetHeight;
      canvas.width = width;
      canvas.height = height;
      context.drawImage(img, 0, 0, width, height);

      var styles = getComputedStyle(gif);

      // Wrap $canvas in a parent container so it can inherit the dynamic width dimension
      if (settings.autoResize === true) {
        canvas.setAttribute('data-src', gif.src);
        canvas.setAttribute('class', 'gif');
        var faux = document.createElement('span');
        faux.classList.add('p-playgif');
        faux.style.float = styles.float;
        faux.style.margin = styles.margin;
        faux.style.padding = styles.padding;
        faux.style.width = styles.width;
        faux.style.height = styles.height;
        faux.style.position = 'relative';
        faux.style.display = 'block';

        canvas.style.position = 'absolute';
        canvas.style.top = '0';
        canvas.style.left = '0';

        // Wrap canvas in container
        faux.appendChild(canvas);
        canvas = faux;
      } else {
        // Inherit styles of GIF
        canvas.style.float = styles.float;
        canvas.style.margin = styles.margin;
        canvas.style.padding = styles.padding;
        canvas.style.display = styles.display;
      }

      // Replace GIF w/ static canvas
      gif.parentNode.replaceChild(canvas, gif);

      // Sometimes the containing element fixed dimensions and cut off
      // portions of the GIF. In this case, use the parent's dimensions
      // to center the circle overlay.
      var parent = canvas.parentNode;
      var parentStyles = getComputedStyle(parent);
      if (parent.tagName !== 'p' &&
          (parentStyles.maxWidth !== 'none' || parentStyles.maxHeight !== 'none')) {
        width = parent.offsetWidth;
        height = parent.offsetHeight;
      }

      if (settings.removeAnchor) {
        if (parent.tagName === 'A' && /\.gif$/i.test(parent.href)) {
          parent.parentNode.replaceChild(canvas, parent);
        }
      }

      // Show GIF overlay if dimensions are greater than 150x150
      if (width > 150 && height > 150 && settings.overlay) {
        drawGIFOverlay(context, width / 2, height / 2);
      }

      // Attach event listeners to canvas and gif
      canvas.addEventListener(settings.on, function() { startAnimation(canvas, gif); });
      gif.addEventListener(settings.off, function() { stopAnimation(canvas, gif); });
    };

    img.src = gif.src;
  }
};
