var { difference } = require('lodash');
var autosave = require('../../../actions/autosave');
var docActions = require('../../../actions/docs');
var getSubdocIds = require('../../../../shared/subdoc_ids');

export default function initSubdocs(docId) {
  var doc = app.state.select(['autosave', 'docsById', docId]);
  if (doc.get().schema_name === 'story') {
    doc.on('update', () => {
      if (doc.get()) {
        loadSubdocs(doc.get());
      }
    });
    loadSubdocs(doc.get());
  }
}

function loadSubdocs(doc) {
  // initialize new subdocs to receive their remote updates
  var currentSubdocIds = app.state.get(['subdocs']).map(({ id }) => id);
  var docSubdocIds = getSubdocIds(doc.head.contents);
  var addedSubdocIds = difference(docSubdocIds, currentSubdocIds);

  if (addedSubdocIds.length === 0) {
    return;
  }

  docActions.searchDocs({
    ids: addedSubdocIds.join(',')
  }).then(({ docs }) => {
    docs.map(doc => autosave.setAndInitDoc(doc));
  });
}
