var { Delta } = require('rich-text');
var { get: getPath } = require('lodash');
var assetActions = require('./assets');
var modalActions = require('./modals');
var editorActions = require('./editor');

export function selectFile(target) {
  modalActions.pushModal({ selectAsset: { type: 'file', target } });
}

export function editFile(file, target) {
  modalActions.pushModal({ editObject: { type: 'file', data: file, target } });
}

export function saveFile(file) {
  return assetActions.saveAsset('file', file);
}

function getFileDelta(docId, file) {
  var title = file.title || 'Untitled';
  return new Delta().insert(title, { link: file.url });
}

export function insertFile({ docId }, file) {
  editorActions.insertAtCaret(docId, getFileDelta(docId, file));
}

export function insertFileAtMouseEvent(docId, event, file) {
  editorActions.insertAtMouseEvent(docId, event, getFileDelta(docId, file));
}

// Get the starting title from the last chunk of the URL
function parseTitleFromUrl(url) {
  var match = url.match(/.*\/(.*?)$/);
  return match ? match[1] : 'Untitled';
}

export function setParams(params) {
  params.title = getPath(params, ['file', 'name']) || parseTitleFromUrl(params.remote_file_url);
}
