var { Delta } = require('rich-text');
var getOffset = require('../../shared/delta/get_offset');

export function setQuillReference(docId, quill, path) {
  window.quills[docId] = quill;
  app.state.set(['autosave', 'quills', docId], { path });
}

export function unsetQuillReference(docId) {
  window.quills[docId] = null;
  app.state.unset(['autosave', 'quills', docId]);
}

export function trackDeltas(docId) {
  app.state.set(['tracked_deltas', docId], []);
}

export function untrackDeltas(docId) {
  app.state.unset(['tracked_deltas', docId]);
}

export function insertObject(docId, lineId, object) {
  var quill = window.quills[docId];
  var delta = quill.getContents();
  var lineOffset = lineId && getOffset(delta, op => op.attributes && op.attributes.id === lineId);

  if (lineOffset) {
    var update = new Delta()
      .retain(lineOffset - 1)
      .delete(1)
      .insert(object);

    quill.focus();
    quill.updateContents(update, 'user');
    quill.setSelection(lineOffset - 1, 1);
  } else {
    insertAtCaret(docId, new Delta().insert(object));
  }
}

// Move caret to the event location, then call insertAtCaret
export function insertAtMouseEvent(docId, event, delta) {
  var selection = window.getSelection();
  var range;

  if (event.rangeParent && event.rangeOffset) { // Firefox
    range = document.createRange();
    range.setStart(event.rangeParent, event.rangeOffset);
  } else if (document.caretRangeFromPoint) { // Others
    range = document.caretRangeFromPoint(event.clientX, event.clientY);
  }

  if (range instanceof window.Range) {
    var quill = window.quills[docId];
    selection.removeAllRanges();
    selection.addRange(range);
    quill.root.focus();
    quill.update();
  }

  return insertAtCaret(docId, delta);
}

export function insertAtCaret(docId, delta) {
  var quill = window.quills[docId];
  if (quill) {
    quill.focus();

    var update = new Delta();
    var sel = quill.getSelection();
    if (sel) {
      update.retain(sel.index);
      update.delete(sel.length);
    } else {
      var contents = quill.getContents();
      update.retain(contents.length());
    }

    update = update.concat(delta);
    return quill.updateContents(update, 'user');
  }
}
