var messages = require('../config/messages');
var messageActions = require('../actions/messages');

let unloading = false;
let reconnectingMessageId = null;

// Official socket.io message for a manual client disconnect
// See: https://github.com/socketio/socket.io-client/blob/master/docs/API.md#event-disconnect
const CLIENT_DISCONNECT = 'io client disconnect';

// Force disconnect reasons
const STALE_TAB = 'stale-tab';

export default function connectionStatus() {
  app.state.set('connected', true);

  // Socket disconnect/reonnect
  app.socket.on('disconnect', onDisconnect);
  app.socket.on('reconnect', () => onReconnect('socket'));
  app.socket.on('reconnect_failed', onReconnectFailure);

  // Content API socket disconnect/reconnect
  app.contentApiSocket.on('disconnect', onDisconnect);
  app.contentApiSocket.on('reconnect', () => onReconnect('contentApiSocket'));
  app.contentApiSocket.on('reconnect_failed', onReconnectFailure);

  // Manually disconnect socket connections to servers when prompted
  app.on('sockets.disconnect', forceDisconnect);

  // prevent firefox from showing `Reconnecting...` message when navigating away
  window.addEventListener('beforeunload', () => {
    unloading = true;
  });
}

function forceDisconnect(reason) {
  app.socket.disconnect();
  app.contentApiSocket.disconnect();
  app.state.set(['connected'], false);
  app.state.set(['socketError'], true);

  if (reason === STALE_TAB) {
    messageActions.showMessage({
      type: 'negative',
      dismissible: false,
      message: messages.RELOAD_STALE_TAB
    });
  }
}

function onDisconnect(reason) {
  // If we've manually disconnect socket connections to the servers don't bother
  // with the reconnect notice since we want the user to refresh
  if (reason === CLIENT_DISCONNECT) {
    return;
  }

  var shouldUpdateMessage = (
    app.state.get(['connected']) &&
    !unloading &&
    !reconnectingMessageId
  );

  app.state.set(['connected'], false);
  if (shouldUpdateMessage) {
    reconnectingMessageId = messageActions.showMessage({
      type: 'cautionary',
      message: messages.RECONNECTING
    });
  }
}

function onReconnect(socketType) {
  // try to reconnect the socket if necessary
  if (app[socketType].disconnected) {
    app[socketType].connect();
  }

  var shouldUpdateMessage = (
    !app.state.get(['connected']) &&
    !unloading &&
    reconnectingMessageId
  );

  app.state.set(['connected'], true);
  if (shouldUpdateMessage) {
    messageActions.removeMessage(reconnectingMessageId);
    reconnectingMessageId = null;
  }
}

// If we fail to reconnect after the set retry amount, notify the user
function onReconnectFailure() {
  // `socketError: true` will trigger a readOnly state on editor pages
  app.state.set(['socketError'], true);
  messageActions.removeMessage(reconnectingMessageId);
  messageActions.showMessage({
    type: 'negative',
    dismissible: false,
    message: messages.RECONNECT_ERROR
  });
}
