var { pick, forOwn } = require('lodash');
var cleanHTML = require('../lib/clean_html');
var request = require('../lib/request');
var requestProgress = require('../lib/request_progress');
var parsePagination = require('../lib/parse_pagination');
var messages = require('../config/messages');
var messageActions = require('./messages');
var fileActions = require('./files');

export const IMAGE_ALT_TEXT_LIMIT = 500;

const ALLOWLIST_CONFIG = {
  image: {
    urlPattern: /\.(jpe?g|gif|png)(\?.+)?$/i,
    mimeTypes: [
      'image/jpeg',
      'image/pjpeg',
      'image/gif',
      'image/png',
      'image/x-png',
    ]
  },
  file: {
    urlPattern: /\.(pdf|docx|mp3?)(\?.+)?$/i,
    mimeTypes: [
      'application/pdf',
      'application/msword',
      'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
      'audio/mpeg',
      'audio/mp3'
    ]
  }
};

function createAsset(type, asset) {
  var data = pick(asset, [
    'title',
    'credit',
    'search_text',
    'network_id',
    'community_id',
    'user_id',
    'source',
    'source_id',
    'source_caption',
    'preview_url',
    'remote_file_url'
  ]);
  if (data.credit) {
    data.credit = cleanHTML(data.credit);
  }
  // We save caption data to the database for Merlin images
  if (data.source === 'merlin') {
    data.caption = asset.caption;
  }

  return request(`/api/assets/${type}s`, { method: 'POST', body: data, opts: { handleError: false } }).catch(err => {
    if (type === 'image' && asset.source === 'merlin' && err.response.status === 503) {
      return handleError(false, messages.MERLIN_API_DOWN);
    }
    return handleError(false);
  });
}

function updateAsset(type, asset) {
  var data = pick(asset, ['title', 'credit', 'search_text', 'source_caption']);
  if (data.credit) {
    data.credit = cleanHTML(data.credit);
  }
  if (type === 'image') {
    var imageAttributes = pick(asset, ['usage_rights']);
    data = Object.assign({}, data, imageAttributes);
  }

  return request(`/api/assets/${type}s/${asset.id}`, {
    method: 'PUT',
    body: data,
    opts: { handleError: false }
  }).catch(() => {
    return handleError(false);
  });
}

export function saveAsset(type, asset) {
  if (type === 'image' && (asset.title || '').length > IMAGE_ALT_TEXT_LIMIT) {
    return handleError(false, messages.ALT_TEXT_OVER_LIMIT.replace('{alt_text_limit}', IMAGE_ALT_TEXT_LIMIT));
  }
  return asset.id ? updateAsset(type, asset) : createAsset(type, asset);
}

export function storeSelectSnapshot(snapshot) {
  app.state.set(['modal', 'selectAsset', 'snapshot'], snapshot);
}

// note: this mutates the `query` object
export function searchAssets(type, query) {
  query.per_page = 12;
  var entitlements = app.state.get(['entitlements']);
  if (entitlements.includes('use_network_asset_search') && query.sources && query.sources.includes('upload')) {
    query.network_id = app.state.get(['community', 'network', 'id']);
  } else {
    query.community_id = app.state.get(['community', 'id']);
  }
  query.deleted = false;
  for (let key of ['ids', 'sources']) {
    if (key in query) {
      query[key] = query[key].join(',');
    }
  }

  return request(`/api/assets/${type}s`, {
    query,
    opts: { withHeaders: true, handleError: false }
  }).then(result => {
    var pagination = parsePagination({
      page: parseInt(result.headers.get('Page')),
      perPage: parseInt(result.headers.get('Per-Page')),
      total: parseInt(result.headers.get('Total'))
    });
    return [result.data, pagination];
  }).catch(err => {
    if (err.response.status === 403 && query.sources && entitlements.includes('use_network_asset_search')) {
      return handleError(false, messages.NETWORK_ASSETS_ACCESS_DENIED);
    }

    if (type === 'image' && query.source === 'getty' && err.response.status === 503) {
      return handleError(false, messages.GETTY_API_DOWN);
    }
    return handleError();
  });
}

export function validateImageUrl(url) {
  return url.match(ALLOWLIST_CONFIG.image.urlPattern);
}

// Validate that:
// A) The incoming `type` is a supported asset type
// B) The file and/or url for the asset type is allowlisted for upload
function assetIsAllowlisted(type, params) {
  var config = ALLOWLIST_CONFIG[type];
  return (
    config &&
    (
      (params.file && (config.mimeTypes.includes(params.file.type) || params.file.name.match(config.urlPattern))) ||
      (params.remote_file_url && params.remote_file_url.match(config.urlPattern))
    )
  );
}

// A bit of a hack to support uploading assets from drag/drop interfaces where multiple
// asset types are supported. If the incoming params to be sent to the chorus-asset-service
// match an allowlist for a supported type, we assume the asset is of that type.
export function getAssetType(params) {
  return Object.keys(ALLOWLIST_CONFIG).find(type => assetIsAllowlisted(type, params));
}

// A) Flash the error message for the asset type if flagged to do so
// B) Otherwise, simply reject with the message to be rendered inline
function handleError(flash = false, message = '') {
  if (flash) {
    if (message) {
      messageActions.showMessage({ type: 'cautionary', message });
    } else {
      messageActions.showError();
    }
  }
  return Promise.reject(new Error(message || messages.UNEXPECTED));
}

function handleUploadError(err, type, flash, isGif = false) {
  // I've added this, but it seems that when use_network_asset_search is true, non-Asset Manager users can still
  // upload images to the community (but not search them :-/)
  if (err.response.status === 403 && app.state.get(['entitlements']).includes('use_network_asset_search')) {
    return handleError(flash, messages.NETWORK_ASSETS_ACCESS_DENIED);
  }

  if (err.response.status === 413) {
    let message = messages.FILE_TOO_LARGE;

    if (isGif) {
      message = messages.GIF_TOO_LARGE.replace('{size}', 12).replace('{platforms}', 'all platforms');
    } else if (type === 'image') {
      message = messages.IMAGE_TOO_LARGE;
    }

    return handleError(flash, message);
  }
  return handleError(flash);
}

// Single asset upload via either file picker or url
export function uploadAsset(type, params, onProgress, flash = true) {
  if (!assetIsAllowlisted(type, params)) {
    return handleError(flash, messages.UNSUPPORTED_ASSET_TYPE);
  }

  var formData = new FormData();
  formData.append('user_id', app.state.get(['user', 'id']));
  formData.append('network_id', app.state.get(['community', 'network', 'id']));
  formData.append('community_id', app.state.get(['community', 'id']));

  if (type === 'file') {
    fileActions.setParams(params);
  }
  forOwn(params, (val, key) => formData.append(key, val));

  const isGif = params.file && params.file.type === 'image/gif'
    || params.remote_file_url && params.remote_file_url.endsWith('.gif');

  return requestProgress(`/api/assets/${type}s`, 'POST', formData, onProgress)
    .catch(err => handleUploadError(err, type, flash, isGif));
}

// Bulk asset upload
export function uploadAssets(type, params, onProgress, flash = true) {
  var formData = new FormData();

  // Run our validation for every file. Reject uploading all files if one fails validation.
  var anyInvalidAssets = false;
  for (let file of Array.from(params.files)) {
    if (!assetIsAllowlisted(type, { file })) {
      anyInvalidAssets = true;
    } else {
      formData.append('files', file);
    }
  }

  if (anyInvalidAssets) {
    return handleError(flash, messages.UPLOAD_INVALID_FILE);
  }

  formData.append('source', params.source);
  formData.append('user_id', app.state.get(['user', 'id']));
  formData.append('network_id', app.state.get(['community', 'network', 'id']));
  formData.append('community_id', app.state.get(['community', 'id']));

  return requestProgress(`/api/assets/${type}s/batch`, 'POST', formData, onProgress)
    .catch(err => handleUploadError(err, type, flash));
}
