export default function initPresence(room, greetUsers) {
  initAnthemServerPresence(room, greetUsers);
  initContentApiServerPresence(room);
}

function initAnthemServerPresence(room, greetUsers) {
  const socket = app.socket;
  const cursor = app.state.select('users');
  const addUser = newUser => {
    if (!cursor.get().find(user => user.id === newUser.id)) {
      cursor.push(newUser);
    }
  };

  socket.on(`presence.${room}.greet`, addUser);
  socket.on(`presence.${room}.joined`, newUser => {
    addUser(newUser);
    socket.emit(`presence.${room}.greet`, newUser.socketId);
  });
  socket.on(`presence.${room}.left`, oldUser => {
    cursor.set(cursor.get().filter(user => user.socketId !== oldUser.socketId));
  });
  socket.on('reconnect', () => {
    cursor.set([]);
    socket.emit('presence.join', room, greetUsers, app.state.get(['user']), addUser);
  });

  socket.emit('presence.join', room, greetUsers, app.state.get(['user']), addUser);
}

function initContentApiServerPresence(room) {
  const contentApiSocket = app.contentApiSocket;
  contentApiSocket.on('reconnect', () => contentApiSocket.emit('presence.join', room));
  contentApiSocket.emit('presence.join', room);
}
