var request = require('../lib/request');
var { get: getPath } = require('lodash');

export function searchHermano(type, value) {
  var prefix = prefixForType(type);
  return request(`${urlFromType(type)}/search.json`, {
    opts: { cache: true },
    query: { keywords: value }
  }).then(response => {
    // format results with full hermano_id string
    return response.data.map((item) => {
      return Object.assign({}, item, { hermano_id: `${prefix}${item.id}` });
    });
  });
}

export function getRecord(type, hermanoId) {
  var id = hermanoId.match(/[^/]+$/)[0];
  return request(`${urlFromType(type)}/search.json?record_id=${id}`, { opts: { cache: true } }).then(response => {
    return response.data[0];
  });
}

export function hermanoIdForMapPoint(mapPoint) {
  var network = app.state.get(['community', 'network', 'slug']);
  var type = network === 'curbed' ? 'building' : 'venue';
  mapPoint.network = network;

  return request(`${urlFromType(type)}/create_or_update`, {
    method: 'POST',
    opts: { handleError: false },
    body: mapPoint
  }).then((response) => prefixForType(response.data.display_type.toLowerCase()) + response.data.id);
}

export function imageForRecord(record) {
  // product: record.image.url
  // game: record.default_release.cover_art.large_url
  // venue: record.image_urls[0]
  return getPath(record, ['image', 'url']) || getPath(record, ['default_release', 'cover_art', 'large_url']) || getPath(record, ['image_urls', 0]) || null;
}

function prefixForType(type) {
  if (type === 'product') {
    return 'Verge::Product/';
  } else if (type === 'game') {
    return 'Polygon::Game/';
  } else if (type === 'venue') {
    return 'Geo::Venue/';
  } else if (type === 'building') {
    return 'Geo::Building/';
  }
}

function urlFromType(type) {
  if (type === 'product') {
    return '/api/hermano/verge/products';
  } else if (type === 'game') {
    return '/api/hermano/polygon/games';
  } else if (type === 'venue') {
    return '/api/hermano/geo/venues';
  } else if (type === 'building') {
    return '/api/hermano/geo/buildings';
  }
}
