var autosize = require('autosize');
var { debounce } = require('lodash');
var iframeReady = require('./iframe_ready');

exports.autosize = function(node, padding = 0) {
  if (node.tagName === 'IFRAME') {
    return autosizeIframe(node, padding);
  } else if (node.tagName === 'TEXTAREA') {
    return autosizeTextarea(node);
  }
};

function autosizeIframe(node, padding) {
  var observer;
  var resize = function() {
    var el = node.contentDocument && node.contentDocument.documentElement;
    if (el) {
      node.style.height = el.scrollHeight + padding + 'px';
      el.style.overflow = 'hidden';
    }
  };

  iframeReady(node).then(function() {
    node.contentWindow.addEventListener('resize', resize);

    // twitter and facebook embeds somehow don't trigger the window resize event
    if (node.contentWindow.twttr) {
      node.contentWindow.twttr.events.bind('rendered', resize);
    }
    if (node.contentWindow.FB) {
      node.contentWindow.FB.Event.subscribe('xfbml.render', resize);
    }

    observer = new MutationObserver(debounce(resize, 200));
    observer.observe(node, { attributes: true });
    observer.observe(node.contentDocument.body, {
      childList: true,
      attributes: true,
      characterData: true,
      subtree: true
    });

    resize();
  }).catch(function() {});

  return {
    teardown: function() {
      try {
        if (observer) {
          observer.disconnect();
        }
        if (node && node.contentWindow) {
          node.contentWindow.removeEventListener('resize', resize);
        }
      } catch (err) {
        console.error(err);
      }
    }
  };
}

function autosizeTextarea(node) {
  autosize(node);

  return {
    teardown: function() {
      autosize.destroy(node);
    }
  };
}
