// Given an image's desired, the image's subject, and a desired ratio,
// return the x, y, width, and height of a crop
// that meets that ratio and centers that subject

module.exports = function(crop, subject, ratio) {
  // by default,
  // crop: x: 0, y: 0, w: (img width), h: (img height)
  // subject: x: (half of width), y: (half of height)

  if (!crop || !subject || !ratio) {
    return;
  }

  var h = Math.min(crop.h, crop.w / ratio);
  h = Math.round(h);

  var w = Math.min(crop.w, crop.h * ratio);
  w = Math.round(w);

  var y = subject.y - h / 2;
  y = Math.max(y, crop.y);
  y = Math.min(y, crop.y + crop.h - h);
  y = Math.round(y);

  var x = subject.x - w / 2;
  x = Math.max(x, crop.x);
  x = Math.min(x, crop.x + crop.w - w);
  x = Math.round(x);

  return { x: x, y: y, w: w, h: h };
};
