const { get: getPath } = require('lodash');

function hasPolicyRole(objectOperation) {
  const user = app.state.get('user');
  const communityId = app.state.get(['community', 'id']);
  const roles = getPath(app.config.policyRoles, objectOperation);

  return hasRoleOnCommunity(user, communityId, roles);
}

function hasRoleOnCommunity(user, communityId, roles) {
  return (
    user.networkRoles.some(r => roles[r]) ||
    user.memberships.some(m => (
      membershipAuthorized(m, roles) &&
      communityId === m.community.id
    ))
  );
}

function membershipAuthorized(membership, roles) {
  return membership.status === 'ACTIVE' && membership.roles.some(r => roles[r]);
}

function isNetworkAdmin() {
  const user = app.state.get('user');
  return user.networkRoles.includes('NETWORK_ADMIN');
}

function isCommunityPostAuthor() {
  const user = app.state.get('user');
  const communityId = app.state.get(['community', 'id']);

  return hasRoleOnCommunity(user, communityId, { 'COMMUNITY_POST_AUTHOR': true });
}

module.exports = {
  hasPolicyRole,
  isNetworkAdmin,
  isCommunityPostAuthor
};
