var config = app.config.partnerPlatforms;
var docActions = require('../actions/docs');

const STRIPPED_SCHEMA_NAMES = ['poll'];

const STATUS_MESSAGES = {
  STRIP: 'will be stripped out on',
  BLOCK: 'will block this story from'
};

// When building the object iframe, we can get the message directly because we know
// we don't need a pluralized message
export function getMessage(object) {
  var warnings = getWarnings(object);
  return getMessageForWarnings(warnings);
}

// Format the appropriate message for set of warnings, allowing for pluralization
export function getMessageForWarnings(warnings, plural = false) {
  if (!warnings) {
    return '';
  }
  return `${plural ? 'These' : 'This'} ${warnings}.`;
}

export function getAllDocWarnings(doc) {
  return docActions.getAllDocObjects(doc, ['oembed', 'doc']).reduce((objects, object) => {
    var warnings = getWarnings(object);
    if (warnings) {
      objects.push({ object, message: warnings });
    }
    return objects;
  }, []);
}

export function hasStatus(message, status, platform) {
  return message.includes(warningForStatus(status, platform));
}

// When a warning message may apply to multiple objects, we get the warnings
// separately from formatting the full message
function getWarnings(object) {
  var type = Object.keys(object)[0];
  var provider = '';
  var html = '';
  var schemaName = '';
  if (type === 'oembed') {
    // use the url hostname as a fallback if the provider name isn't present
    provider = getProvider(object.oembed);
    html = object.oembed.html || '';
  } else if (type === 'doc') {
    schemaName = object.doc.type;
  }

  var googleAMP = googleAMPStatus(provider, html, schemaName);
  var appleNews = appleNewsStatus(provider, html, schemaName);

  if (!googleAMP && !appleNews) {
    return '';
  } else if (googleAMP === appleNews) {
    return warningForStatus(googleAMP, 'Google AMP and Apple News');
  }

  var warnings = [];
  if (googleAMP) {
    warnings.push(warningForStatus(googleAMP, 'Google AMP'));
  }
  if (appleNews) {
    warnings.push(warningForStatus(appleNews, 'Apple News'));
  }
  return warnings.join(' and ');
}

function getProvider(oembed) {
  return (oembed.provider_name || new URL(oembed.url).hostname || '').toLowerCase();
}

function warningForStatus(status, platform) {
  return STATUS_MESSAGES[status] + ' ' + platform;
}

function validateProvider(providers, provider) {
  // because we use the hostname as a fallback, the config provider
  // may be a substring of the hostname
  return providers.some((p) => provider.includes(p));
}

function validateHTML(selector, html) {
  var div = document.createElement('div');
  div.innerHTML = html;
  return div.querySelectorAll(selector).length > 0;
}

function validateSubdoc(schemaName) {
  return STRIPPED_SCHEMA_NAMES.includes(schemaName);
}

function googleAMPStatus(provider, html, schemaName) {
  if (!app.state.get(['community', 'publishForGoogleAmp'])) {
    return;
  } else if (
    validateProvider(config.google_amp.blocklist, provider) ||
    validateHTML('iframe[src^="http://"]', html)
  ) {
    return 'BLOCK';
  } else if (
    provider === 'vox media' &&
    !validateHTML('div[data-iframe^="https://"]', html)
  ) {
    // Block story if vox media embed code is missing iframe for AMP
    // Embed rules: https://github.com/voxmedia/voxmedia-middleman-shared/wiki/Embeddable-graphics-and-partner-platforms
    return 'BLOCK';
  } else if (
    validateSubdoc(schemaName) ||
    (
      provider && // an empty provider shouldn't pass this test
      !validateProvider(config.google_amp.allowlist, provider) &&
      !validateHTML('iframe[src^="https://"]', html) &&
      !validateHTML('iframe[src^="//"]', html)
    )
  ) {
    return 'STRIP';
  }
}

function appleNewsStatus(provider, html, schemaName) {
  if (!app.state.get(['community', 'publishForAppleNews'])) {
    return;
  } else if (
    validateProvider(config.apple_news.blocklist, provider) ||
    validateHTML('iframe[src*="docs.google.com"]', html) // multiple providers may use google docs iframes
  ) {
    return 'STRIP';
  } else if (
    provider === 'vox media' &&
    !validateHTML('div[data-iframe-fallback^="https://"]', html)
  ) {
    // Block story if vox media embed code is missing image fallback for Apple News
    // Embed rules: https://github.com/voxmedia/voxmedia-middleman-shared/wiki/Embeddable-graphics-and-partner-platforms
    return 'STRIP';
  } else if (
    validateSubdoc(schemaName) ||
    (
      provider && // an empty provider shouldn't pass this test
      !validateProvider(config.apple_news.allowlist, provider)
    )
  ) {
    return 'STRIP';
  }
}
