export default function iframeReady(iframe) {
  if (!iframe) {
    return Promise.reject(new Error('iframeReady(): argument must not be ' + iframe));
  }

  return new Promise((resolve, reject) => {
    var interval = null;
    var timeout = null;

    try {
      if (iframeIsReady(iframe)) {
        return resolve();
      }

      // Poll iframe every 50ms to see if ready
      interval = setInterval(() => {
        if (iframeIsReady(iframe)) {
          clearInterval(interval);
          clearTimeout(timeout);
          resolve();
        }
      }, 50);

      // Timeout after 10 seconds
      timeout = setTimeout(() => {
        clearInterval(interval);
        reject(new Error('iframeReady(): timed out waiting for iframe to become ready'));
      }, 10000);
    } catch (err) {
      clearInterval(interval);
      clearTimeout(timeout);
      reject(err);
    }
  });
}

// An iframe is deemed ready if:
// A) The document is not blank
// B) The DOM is `interactive` or all markup and content has been rendered
function iframeIsReady(iframe) {
  var doc = iframe.contentDocument;
  return (
    doc &&
    doc.URL !== 'about:blank' &&
    (doc.readyState === 'interactive' || doc.readyState === 'complete')
  );
}
